import React from 'react';

import SEO from '../components/seo';
import Fallback from '../components/Fallback';

const CliendSideOnlyLazzy = React.lazy(() => new Promise(resolve => {
  setTimeout(() => resolve(import('../views/Home')), 200);
}));

const IndexPage = () => {
  const isSSR = typeof window === "undefined";
  return (
    <>
      <SEO
        title="Minzard Dillah - Full Stack Developer"
        description="Hello my name is Minzard, Currently, I work as the Front-end Developer at Jagoapa"
        meta={[
          {
            name: 'keywords',
            content: [`minzard`, `minzard dillah`, `muhammad minzard dillah`].join(', ')
          }
        ]}
      />
      {!isSSR && (
        <React.Suspense fallback={<Fallback />}>
          <CliendSideOnlyLazzy />
        </React.Suspense>
      )}
    </>
  );
};

export default IndexPage;
